<template>
  <h3>Todos</h3>
  <div class="legend">
    <span>Double Click to mark as complete</span>
    <span>
        <span class="incomplete-box"></span> = Incomplete
    </span>

    <span>
        <span class="complete-box"></span> = Complete
    </span>
  </div>
  <div class="todos">
    <div @dblclick="onDblClick(todo)" v-for="todo in allTodos" :key="todo.id" class="todo"
    :class="{'is-complete':todo.completed}">
        {{todo.title}}
         <TrashIcon class="icon-trash" @click="deleteTodo(todo.id)"/>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapActions } from 'vuex'
import { TrashIcon } from '@heroicons/vue/24/solid'
export default {
    name:"Todos",
    components:{
        TrashIcon
    },

    //because we are going to have multiple methods we may use curly braces and spread the mapActions, otherwise the getters example is sufficient

    methods: {
        ...mapActions(['fetchTodos','deleteTodo','updateTodo']),

        onDblClick(todo){
            const updTodo = {
                id:todo.id,
                title: todo.title,
                completed: !todo.completed
            }

            this.updateTodo(updTodo)
        }
    },

    computed: mapGetters(['allTodos']),
    created(){
        this.fetchTodos()
    }
}
</script>

<style scoped>
.todos{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
}
.todo {
    border: px solid #ccc;
    background: #41b883;
    padding: 1rem;
    border-radius:5px;
    text-align: center;
    position:relative;
    cursor:pointer;
}
.icon-trash {
    position:absolute;
    right:10px;
    bottom:10px;
    color:#fff;
    cursor: pointer;
    height: 30px;
    width:20px;
}

.legend{
    display: flex;
    justify-content: space-around;
    margin-bottom: 1rem;
}
.complete-box {
    display: inline-block;
    width:10px;
    height: 10px;
    background: #35495e;
}
.incomplete-box{
    display: inline-block;
    width: 10px;
    height: 10px;
    background:#41b883;
}
.is-complete{
    background: #35495e;
    color:#fff;
}

@media (max-width:500px){
    .todos{
        grid-template-columns: 1fr;
    }
}

</style>